.gpt3__navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;

    padding: 2rem 6rem;
}

.gpt3__navbar-links {
    flex: 1;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.gpt3__navbar-links_logo {
    margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
    width: 62.56px;
    height: 16.02px;
}

.gpt3__navbar-links_container{
    display: flex;
    flex-direction: row;
}

.gpt3__navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign p,
.gpt3__navbar-menu_container p{
    color: #fff;
    font-family:  var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
    transition: all 0.3s;
    padding: 0.2rem 0.5rem;
}

.gpt3__navbar-links_container p:hover,
.gpt3__navbar-sign p:hover,
.gpt3__navbar-menu_container p:hover{
    box-shadow: 0 0 5px rgb(255, 255, 255);
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button{
    padding: 0.5rem 1rem;
    color: #fff;
    background-color: #ff4820;
    border: none;
    outline: none;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s;
}

.gpt3__navbar-sign button:hover,
.gpt3__navbar-menu_container button:hover{
    box-shadow: 0 0 10px #ef9c8a;
}

.gpt3__navbar-menu{
    display: none;
    margin-left: 1rem;
    position: relative;
}

.gpt3__navbar-menu svg{
    cursor: pointer;
}

.gpt3__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background-color: var(--color-footer);
    position: absolute;
    top: 40px;
    right: 0;
    padding: 20px;
    margin-top: 1rem;
    border-radius: 5px;
    min-width: 210px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.gpt3__navbar-menu_container p{
    margin: 1rem 0;
}
.gpt3__navbar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container{
        display: none;
    }
    .gpt3__navbar-menu{
        display: flex;
    }
}
@media screen and (max-width: 700px) {
   .gpt3__navbar{
    padding: 2rem 4rem;
   }
}
@media screen and (max-width: 550px) {
    .gpt3__navbar{
        padding: 2rem;
    }
    .gpt3__navbar-sign{
        display: none;
    }
    .gpt3__navbar-menu_container-links-sign{
        display: block;
    }
}