.gpt3__possibility{
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.gpt3__possibility-image img{
    width: 90%;
    height: 90%;
}

.gpt3__possibility-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.gpt3__possibility-content h4{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    color: #71e5ff;
}
.gpt3__possibility-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    margin: 1rem 0;
}
.gpt3__possibility-content p{
    font-family: var(--font-family);
    font-size: 16px;
    color: var(--color-text);
    margin-bottom: 2rem;

}
.gpt3__possibility-content h4:last-child{
    color: #ff8a71;
    cursor: pointer;
    transition: all 0.3s;
    border-radius: 10px;
    padding: 0.5rem;
}

.gpt3__possibility-content h4:last-child:hover{
    box-shadow: 0 0 10px #ff8a71;
}

@media screen and (max-width: 950px){
    .gpt3__possibility{
        flex-direction: column;
    }
    .gpt3__possibility-image{
        margin: 1rem 0;
    }
    .gpt3__possibility-content{
        margin-top: 2rem;
    }
    .gpt3__possibility-content p{
        text-align: justify;
    }
    
    .gpt3__possibility-content h1{
        font-size: 30px;
    }
}